import { strTrim } from './str-trim.js';

/**
 * @description
 * Formats a string.
 *
 * @param str The string to format.
 * @param pattern The formatting pattern. Default is '### #### ####'.
 * @returns The formatted string.
 *
 * @docsCategory preference/utils/string
 * @codeFilter str-format
 */
export const strFormat = (str: string, pattern = '### #### ####'): string => {
  str = str || '';
  let i = 0;
  const matchRegex = strTrim(pattern.replace(/#/g, '')[0]);
  const regexStr = matchRegex ? `#|\\${matchRegex}` : '#';
  const result = pattern.replace(new RegExp(regexStr, 'g'), (match) => {
    return (match === '#' ? str[i++] : pattern[i++]) || '';
  });
  return strTrim(result);
};
